import Vue from 'vue'
import Vuex from 'vuex'
import VueResource from 'vue-resource'
import info from '../json/message'
Vue.use(Vuex)
Vue.use(VueResource)

const store=new Vuex.Store({
	state:{
		personalInfo:info
	}
})

export default store


