import Vue from 'vue'
import VueRouter from 'vue-router'
Vue.use(VueRouter)

const router=new VueRouter({
	routes:[
	{
		path:'/',component:require('../view/mainBody')
	},{
		path:'/introduction',component:require('../view/introduction')
	},{
		path:'/course',component:require('../view/course')
	}]
})

export default router



